@import url(app.css);

:root {
  --light-blue: #e1e8f0;
  --white: #fff;
  --wave-bg: linear-gradient(45deg, #4a90e2, #3a7bd5, #6fc1ff);
}

footer {
  background: linear-gradient(180deg, var(--light-text-color) 0%, #e6ecf0 100%);
  color: black;
  border-top: 3px solid var(--light-blue);
  font-family: "Poppins", sans-serif;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: auto;
  padding: 3rem 1.5rem;
  gap: 2rem;
}

.footer h3 {
  color: var(--bold-text-color);
  margin-bottom: 1.2rem;
  font-size: 1.4rem;
  font-weight: 600;
  border-left: 4px solid var(--light-blue);
  padding-left: 0.6rem;
}

.footer a {
  color: black;
  text-decoration: none;
  transition: color 0.3s ease, transform 0.2s ease;
}

.footer a:hover {
  color: var(--dark-blue);
  transform: translateX(3px);
}

.footer-links ul {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.footer-links li {
  list-style: none;
  font-size: 1rem;
}

.social ul {
  display: flex;
  gap: 1.2rem;
  flex-wrap: wrap;
  justify-content: center;
}

.social li {
  border-radius: 50%;
  width: 42px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s ease;
}

.social li:hover {
  transform: scale(1.1);
}

.social a {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.social svg {
  font-size: 1.3rem;
}

.facebook {
  background-color: #3b5998;
  box-shadow: 0 2px 8px rgba(59, 89, 152, 0.3);
}

.instagram {
  background: linear-gradient(180deg,
      rgba(64, 93, 230, 1) 0%,
      rgba(131, 58, 180, 1) 25%,
      rgba(225, 48, 108, 1) 50%,
      rgba(245, 96, 64, 1) 75%,
      rgba(252, 175, 69, 1) 100%);
  box-shadow: 0 2px 8px rgba(225, 48, 108, 0.3);
}

.youtube {
  background-color: #ff0000;
  box-shadow: 0 2px 8px rgba(255, 0, 0, 0.3);
}

.footer-bottom {
  text-align: center;
  background-color: var(--bold-text-color);
  color: var(--light-blue);
  padding: 0.8rem 0;
  font-size: 0.95rem;
  letter-spacing: 0.3px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.footer-bottom a {
  color: var(--light-blue);
  font-weight: 600;
}

.footer-bottom a:hover {
  color: var(--dark-blue);
  text-decoration: underline;
}

@media screen and (max-width: 768px) {
  .footer {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }

  .footer h3 {
    border: none;
    padding-left: 0;
  }

  .footer-links ul {
    align-items: center;
  }

  .social ul {
    justify-content: center;
  }
}

.linkedin {
  background-color: #0077b5;
  box-shadow: 0 2px 8px rgba(0, 119, 181, 0.3);
}

.github {
  background-color: #171515;
  box-shadow: 0 2px 8px rgba(23, 21, 21, 0.3);
}