/* ---------- Variables ---------- */
:root {
  --ink: #141625;
  --ink-soft: #32344a;

  --c1: #b889ff;
  --c2: #9bd0ff;
  --c3: #ff9fd1;

  --glass-bg: rgba(255, 255, 255, 0.18);
  --glass-border: rgba(255, 255, 255, 0.55);
  --card-bg: rgba(255, 255, 255, 0.38);
  --card-border: rgba(255, 255, 255, 0.55);

  --accent: #288ca7;
  --accent-hover: #215a88;
  --input-focus: #9bd0ff;
}

/* ---------- Base ---------- */
* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  margin: 0;
  font-family: Inter, system-ui, -apple-system, Segoe UI, Roboto, "Helvetica Neue", Arial, sans-serif;
  color: var(--ink);
  overflow-x: hidden;
  background: #eff2f9;
}

/* ---------- Background ---------- */
.bg {
  position: fixed;
  inset: 0;
  overflow: hidden;
  z-index: -2;
}

.blob {
  position: absolute;
  width: 60vmax;
  height: 60vmax;
  filter: blur(80px);
  opacity: 0.65;
  transform: translate3d(0, 0, 0);
  will-change: transform;
}

.b1 {
  left: -10vmax;
  top: -8vmax;
  background: radial-gradient(circle, var(--c1), transparent 60%);
  animation: float1 18s ease-in-out infinite alternate;
}

.b2 {
  right: -12vmax;
  bottom: -10vmax;
  background: radial-gradient(circle, var(--c2), transparent 60%);
  animation: float2 20s ease-in-out infinite alternate;
}

.b3 {
  left: 15vmax;
  bottom: -8vmax;
  background: radial-gradient(circle, var(--c3), transparent 60%);
  animation: float3 22s ease-in-out infinite alternate;
}

@keyframes float1 {
  from {
    transform: translate(0, 0) scale(1);
  }

  to {
    transform: translate(6vmax, 4vmax) scale(1.08);
  }
}

@keyframes float2 {
  from {
    transform: translate(0, 0) scale(1);
  }

  to {
    transform: translate(-5vmax, -3vmax) scale(1.06);
  }
}

@keyframes float3 {
  from {
    transform: translate(0, 0) scale(1);
  }

  to {
    transform: translate(2vmax, -4vmax) scale(1.08);
  }
}

.grain {
  position: absolute;
  inset: 0;
  pointer-events: none;
  opacity: 0.35;
  mix-blend-mode: overlay;
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='160' height='160' viewBox='0 0 160 160'><filter id='n'><feTurbulence type='fractalNoise' baseFrequency='0.8' numOctaves='2' stitchTiles='stitch'/><feColorMatrix type='saturate' values='0'/><feComponentTransfer><feFuncA type='table' tableValues='0 0 0 0 .15 .25 .35 .45 .55 .65 .75 1'/></feComponentTransfer></filter><rect width='100%' height='100%' filter='url(%23n)' opacity='0.7'/></svg>");
  background-size: 220px 220px;
}

/* ---------- Container ---------- */
.contact-container {
  position: relative;
  width: min(90vw, 480px);
  background: var(--card-bg);
  border-radius: 22px;
  border: 1px solid var(--card-border);
  backdrop-filter: blur(18px);
  -webkit-backdrop-filter: blur(18px);
  box-shadow: 0 30px 60px rgba(10, 12, 28, 0.18), inset 0 1px 0 rgba(255, 255, 255, 0.5);
  padding: 2rem 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

/* ---------- Headings ---------- */
.form-heading {
  text-align: center;
  font-size: clamp(1.6rem, 2.6vw, 2.2rem);
  margin-bottom: 1rem;
}

/* ---------- Inputs ---------- */
.field {
  position: relative;
  border-radius: 16px;
  background: linear-gradient(180deg, rgba(255, 255, 255, .85), rgba(240, 245, 255, .75));
  box-shadow: 0 14px 26px rgba(20, 22, 37, .16), inset 0 10px 18px rgba(20, 60, 120, .12), inset 0 -6px 10px rgba(255, 255, 255, .9);
  border: 1px solid rgba(255, 255, 255, .8);
  padding: 10px;
  margin-bottom: 16px;
  transition: all 0.3s ease;
}

.field input,
.field textarea {
  width: 100%;
  font-size: 1rem;
  border: none;
  outline: none;
  background: transparent;
  color: var(--ink);
  padding: 12px 14px;
  border-radius: 12px;
  transition: all 0.3s ease;
}

.field:hover {
  border-color: var(--accent-hover);
  box-shadow: 0 14px 26px rgba(20, 22, 37, .16), inset 0 10px 18px rgba(20, 60, 120, .15), inset 0 -6px 10px rgba(255, 255, 255, .95);
}

.field:focus-within {
  box-shadow: 0 18px 30px rgba(20, 22, 37, .2), inset 0 12px 20px rgba(20, 60, 120, .14), inset 0 -8px 12px rgba(255, 255, 255, .95), 0 0 0 3px var(--input-focus);
  border-color: var(--input-focus);
}

/* ---------- Buttons ---------- */
.glass-btn {
  position: relative;
  width: 100%;
  border-radius: 14px;
  padding: 14px 16px;
  border: 1px solid var(--glass-border);
  background: var(--glass-bg);
  color: var(--ink);
  font-weight: 700;
  cursor: pointer;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  transition: all 0.25s ease;
  box-shadow: 0 8px 24px rgba(14, 18, 50, .18), inset 0 1px 0 rgba(255, 255, 255, .5);
  overflow: hidden;
}

.glass-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 14px 28px rgba(14, 18, 50, .22), inset 0 1px 0 rgba(255, 255, 255, .6);
  background: rgba(255, 255, 255, .26);
  border-color: var(--accent);
  color: var(--accent);
}

.glass-btn:active {
  transform: translateY(1px);
  box-shadow: 0 6px 14px rgba(14, 18, 50, .18), inset 0 1px 0 rgba(255, 255, 255, .55);
}

/* ---------- Success ---------- */
.form-success {
  text-align: center;
  background-color: #d4edda;
  border: 1px solid #28a745;
  color: #155724;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  opacity: 0;
  transform: translateY(-10px);
  transition: all 0.5s ease-in-out;
}

.animate-success {
  opacity: 1;
  transform: translateY(0);
}

/* ---------- Responsive ---------- */
@media (max-width: 600px) {
  .contact-container {
    width: 90%;
    padding: 1.5rem 1.5rem;
  }
}